import React,{ useState } from 'react'; 
import { connect, useSelector } from "react-redux";
import Dog from '../Dog/Dog';
import '../Dogs/Dogs.css';

function Dogs(props){
  const total = useSelector((state)=> state.resultados)
  const[page,setPage]= useState(0)
  const[max, setMax]= useState(172)
  
  const nextPage=()=> {page<max && setPage(page+6)}
  const prevPage=()=> {page>0 && setPage(page-6)}
    return(
      <div>
        <div className='all'>
            {total.slice(page,page+6).map(dog => <Dog
            name={dog.name}
            img={dog.img}
            temperaments={dog.temperament}
            id={dog.id}
          /> )}
        </div>
          <div>
          <button onClick={prevPage}>{"<<--"}</button>
          <button onClick={nextPage}>{"-->>"}</button>
        </div>
        </div>
    )
}
export default Dogs